.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

.AppBody {
    width: 600px;
    margin: 0 auto;
}

.AppBody-fb-db {
    text-align: left;
    border-radius: 4px;
    padding: 15px;
    background: #f1f1f1
}

.AppBody .file {
    padding: .5rem .75rem;
    font-size: 1rem;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.15);
}

.App-form {
    text-align: left;
    border-radius: 4px;
}

.App-form .text {
    padding: .5rem .75rem;
    font-size: 1rem;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.15);
    width: 490px;
}

.App-form .button {
    margin-left: 10px;
    color: #fff;
    background-color: #0275d8;
    border-color: #0275d8;
    white-space: nowrap;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .5rem 1rem;
    font-size: 1rem;
    transition: all .2s ease-in-out;
}

.AppBody .file {
    padding: .5rem .75rem;
    font-size: 1rem;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.15);
    margin-bottom: 20px;
}

.App-images {
    text-align: left;
    border: 4px solid #f1f1f1;
    border-radius: 4px;
    padding: 5px;
    margin-bottom: 50px;
}

.App-image {
    margin: 3px;
    width: 193px;
    vertical-align: top;
    border-radius: 4px;
}
.textfield {
    background-color: #000;
